@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
html {
  background: url(/static/media/bg.37c17300.png) no-repeat center center fixed;
  background-size: cover;
}
#header {
  color: white;
  font-family: 'Bebas Neue';
  font-size: 300px;
  line-height: 130px;
  padding-top: 225px;
  padding-left: 120px;
}
#bottom-header {
  color: white;
  font-family: 'Source Sans Pro';
  font-size: 126px;
  padding-left: 120px;
}
#navdiv {
  width: 600px;
  position: fixed;
  right: 98px;
  font-family: 'Open Sans';
}
#navbar {
  list-style: none;
  display: flex;
  justify-content: space-between;
  text-align: right;
}
#clickable {
  background-color: rgba(255, 255, 255, 0.14);
  color: white;
  font-size: 20px;
  padding: 12px;
  padding-left: 30px;
  padding-right: 30px;
  border-radius: 50px;
  text-align: center;
  transition: 0.5s;
}
a {
  text-decoration: none;
  color: white;
}
#clickable:hover {
  background-color: rgba(255, 255, 255, 0.26);
}

